.about-hero {
  position: relative;
  margin-bottom: 100px;
  .content {
    background: url("../../img/about-bg.webp") center / cover no-repeat;
    padding-block: 35px;
    padding-inline: 10px;
    border-radius: clamp(10px, 2vw, 20px);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: clamp(25px, 4vw, 45px);
    line-height: 97%;
    letter-spacing: -0.01em;
    text-align: center;
    color: var(--gray);
    margin: 0;
    text-align: center;
    max-width: 586px;
    margin: 0 auto;
    margin-bottom: 16px;
  }
  p {
    margin: 0;
    margin-bottom: 20px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 97%;
    letter-spacing: -0.01em;
    text-align: center;
    color: var(--gray);
    opacity: 0.5;
    text-align: center;
  }

  .btn {
    background: var(--accent);
    padding-block: 12px;
    color: var(--white);
    max-width: 250px;
    width: 100%;
    @media (any-hover: hover) {
      &:hover {
        background: var(--hovered);
      }
    }

    &:active {
      background: var(--active);
    }
  }
}

.about-beniffits {
  margin-bottom: 100px;

  .title__section {
    margin-bottom: 60px;
  }
  .items {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
    @media (max-width: 992px) {
      grid-template-columns: 1fr 1fr;
    }
    @media (max-width: 767px) {
      grid-template-columns: 1fr;
    }
    .col {
      display: flex;
      flex-direction: column;
      gap: 20px;

      .image {
        background: var(--blue);
        border-radius: clamp(10px, 2vw, 20px);
        position: relative;
        width: 100%;
        height: 150px;
        img {
          position: absolute;
          scale: 1.2;
          width: 100%;
          height: 100%;
          object-fit: contain;
          bottom: 17px;
          right: 2px;
          @media (max-width: 767px) {
            right: 27px;
            width: fit-content;
          }
        }
      }
    }
  }

  .item {
    display: flex;
    flex-direction: column;
    position: relative;
    background: #f8f8f8;
    height: 335px;
    border-radius: clamp(10px, 2vw, 20px);
    .item-content {
      padding: 35px 20px;
    }

    .item-icon {
      position: absolute;
      top: -10px;
      left: 31px;
      background: var(--accent);
      border-radius: 5px;
      width: 36px;
      height: 36px;
      display: grid;
      place-items: center;

      svg {
        fill: var(--white);
      }
    }

    .item-title {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: clamp(18px, 3vw, 25px);
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--gray);
      margin-bottom: 15px;
    }

    .item-desc {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 14px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--gray);
    }

    .item-image {
      position: absolute;
      bottom: 0;
      @media (max-width: 767px) {
        right: 0;
      }
    }

    &.accent {
      background: var(--blue);
      height: auto;
      .item-title {
        color: var(--accent);
      }
      .item-desc {
        color: var(--accent);
      }
    }
  }
}

.about-comapny {
  margin-bottom: 100px;

  .top {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 20px;
    flex-wrap: wrap;
    margin-bottom: 60px;
    .subtitle {
      display: flex;
      align-items: center;
      gap: 5px;
      background: var(--blue);
      color: var(--accent);
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 14px;
      line-height: 97%;
      letter-spacing: -0.01em;
      max-width: fit-content;
      flex: 1 1 auto;
      padding: 10px 20px;
      border-radius: 100px;

      svg {
        fill: var(--accent);
      }
    }

    .right {
      max-width: 680px;
      display: flex;
      flex-direction: column;
      gap: 12px;

      h2,
      p {
        margin: 0;
      }

      p {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 14px;
        line-height: 97%;
        letter-spacing: -0.01em;
        color: var(--gray);
      }
    }
  }

  .items {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
    @media (max-width: 992px) {
      overflow: auto;
      display: flex;
    }
    .item {
      position: relative;
      @media (max-width: 992px) {
        flex-shrink: 0;
      }
      .item-phone {
        position: absolute;
        top: 15px;
        right: 15px;
        border-radius: 5px;
        background: rgba(255, 255, 255, 0.5);
        width: 40px;
        height: 40px;
        display: grid;
        place-items: center;

        transition: var(--transition);
        @media (any-hover: hover) {
          &:hover {
            background: var(--accent);
          }
        }
      }

      .content {
        position: absolute;
        bottom: 16px;
        left: 12px;
        right: 12px;
        max-width: 261px;
        width: 100%;
        background: var(--white);
        display: grid;
        grid-template-columns: 1fr 40px;
        gap: 20px;
        justify-content: space-between;
        padding: 12px 17px;
        border-radius: clamp(10px, 2vw, 20px);
        .name {
          font-family: var(--font-family);
          font-weight: 500;
          font-size: 16px;
          line-height: 97%;
          letter-spacing: -0.01em;
          color: var(--gray);
          display: flex;
          flex-direction: column;
          gap: 5px;

          span {
            font-family: var(--font-family);
            font-weight: 400;
            font-size: 12px;
            line-height: 97%;
            letter-spacing: -0.01em;
            color: var(--gray);
            opacity: 0.5;
          }
        }

        .icon {
          display: grid;
          min-width: 40px;
          height: 40px;
          border-radius: 5px;
          background: #000;
          place-items: center;
        }
      }
    }

    .item-photo {
      border-radius: clamp(10px, 2vw, 20px);
      overflow: hidden;
      img {
        border-radius: clamp(10px, 2vw, 20px);
      }
    }
  }
}

.about-futures {
  margin-bottom: 100px;
  &__items {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    @media (max-width: 992px) {
      grid-template-columns: 1fr 1fr;
    }
    @media (max-width: 767px) {
      grid-template-columns: 1fr;
    }
  }
  .title__section {
    margin-bottom: 40px;
  }
  .item {
    display: flex;
    flex-direction: column;
    background: #f8f8f8;
    border-radius: clamp(10px, 2vw, 20px);
    position: relative;
    .item-title {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: clamp(16px, 2vw, 25px);
      line-height: 97%;
      letter-spacing: -0.01em;
      color: #272727;
      padding: 30px 20px;
      @media (max-width: 992px) {
        padding: 20px;
      }
    }

    .item-image {
      display: flex;
      justify-content: center;
      align-items: cec;
    }

    &.accent {
      background: var(--blue);
      color: var(--accent);
    }

    &.item-2 {
      grid-column: span 2;
      background: transparent;
      overflow: hidden;
      padding-bottom: 200px;

      @media (max-width: 992px) {
        order: 1;
      }
      @media (max-width: 767px) {
        grid-column: auto;
      }
      .item-bg {
        position: absolute;
        inset: 0;
        z-index: -1;
      }
    }

    &.item-yellow {
      background: #000;
      background: #fdffdd;
      position: relative;
      @media (max-width: 767px) {
        padding-bottom: 40%;
      }
      .item-title {
        color: #e7992c;
      }

      &:after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 151px;
        background: url("../../img/close.webp") center center no-repeat;
        background-size: cover;
      }
    }
  }

  .col {
    display: flex;
    flex-direction: column;
    gap: 13px;
    .image {
      background: var(--blue);
      position: relative;
      width: 100%;
      height: 100%;
      border-radius: clamp(10px, 2vw, 20px);
      display: flex;
      justify-content: center;

      img {
        scale: 1.1;
        transform: translateY(-5px);
      }
    }
  }
}

.about-history {
  margin-bottom: 100px;
  &__head {
    margin-bottom: 40px;
  }

  &__slider {
    max-width: 1565px;
    margin-left: auto;
    position: relative;

    &:after {
      content: "";
      position: absolute;
      bottom: -50px;
      left: 60px;
      width: 100%;
      height: 50px;
      background: url("../../img/icons/line-slider.svg") no-repeat;
      background-size: cover;
    }
  }

  &__slide {
    background: var(--blue);
    box-sizing: border-box;
    padding: 35px 30px;
    border-radius: clamp(10px, 2vw, 30px);

    .year {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: 30px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--accent);
      margin-bottom: 38px;
    }
    .desc {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 14px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--accent);
    }
  }
}

.about-brand {
  margin-bottom: 100px;

  &__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 40px;
  }

  .left {
    display: flex;
    flex-direction: column;
  }

  .subtitle {
    display: flex;
    align-items: center;
    max-width: fit-content;
    background: var(--blue);
    border-radius: 100px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 97%;
    letter-spacing: -0.01em;
    color: var(--accent);
    padding: 10px 22px;
    margin-bottom: 125px;
  }

  &__title {
    margin-bottom: 20px;
  }
  .desc {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 97%;
    letter-spacing: -0.01em;
    color: var(--gray);
    opacity: 0.5;
    flex: 1 1 auto;
  }
  .peoples {
    display: flex;
    align-items: center;
    gap: 10px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 12px;
    line-height: 136%;
    letter-spacing: -0.02em;
    color: #2d2d2d;
    border-radius: 20px;
    background: #f8f8f8;
    padding: 10px;
    border-radius: 10px;
    max-width: 370px;
  }
}

.about-reviews {
  background: #f8f8f8;
  padding-block: 60px;
  margin-bottom: 100px;
  &__top {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    margin-bottom: 40px;
  }

  &__title {
    flex: 1 1 auto;
  }

  .send_review {
    font-family: var(--second-family);
    font-weight: 700;
    font-size: 18px;
    line-height: 110%;
    letter-spacing: -0.02em;
    color: var(--accent);
    margin-left: auto;
    transition: var(--transition);
    @media (any-hover: hover) {
      &:hover {
        color: var(--hovered);
      }
    }
  }

  &__nav {
    display: flex;
    align-items: center;
    gap: 20px;

    &-btn {
      border-radius: 5px;
      width: 40px;
      height: 40px;
      display: grid;
      place-items: center;
      transition: var(--transition);

      @media (any-hover: hover) {
        &:hover {
          background: var(--accent);

          svg path {
            fill: #fff;
          }
        }
      }
    }
  }

  &__slide {
    border-radius: 20px;
    background: var(--white);
    padding: 45px 30px;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    height: auto;

    .head {
      display: grid;
      grid-template-columns: 50px 1fr;
      gap: 15px;

      .photo {
        max-width: 50px;
        border-radius: 100%;
      }

      .info {
        display: flex;
        flex-direction: column;
        gap: 5px;

        .name {
          display: flex;
          align-items: center;
          gap: 10px;
          font-family: var(--second-family);
          font-weight: 700;
          font-size: 22px;
          line-height: 110%;
          letter-spacing: -0.02em;
          color: var(--accent);
        }
      }
    }

    .desc {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 16px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--gray);
      margin-top: 28px;
      padding-left: 65px;
    }
  }
}

.contacts {
  margin-bottom: 100px;

  .wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: clamp(20px, 3vw, 40px);
  }

  .content {
    display: flex;
    flex-direction: column;
    gap: 20px;

    .mail {
      display: flex;
      align-items: center;
      gap: 5px;
      font-family: var(--second-family);
      font-weight: 700;
      font-size: 22px;
      line-height: 110%;
      letter-spacing: -0.02em;
      color: var(--gray);

      transition: var(--transition);

      @media (any-hover: hover) {
        &:hover {
          color: var(--accent);
        }
      }
    }

    .map {
      border-radius: 20px;
      overflow: hidden;
    }

    .socials {
      display: flex;
      align-items: center;
      gap: 10px;

      a {
        display: flex;
        align-items: center;
        gap: 5px;
        font-family: var(--second-family);
        font-weight: 700;
        font-size: 22px;
        line-height: 110%;
        letter-spacing: -0.02em;
        color: #41a469;

        transition: opacity var(--transition);
        will-change: opacity;
        &.telegram {
          color: var(--accent);
        }
        &.whatsapp {
          color: #41a469;
        }

        @media (any-hover: hover) {
          &:hover {
            opacity: 0.7;
          }
        }
      }
    }
  }

  &__carts {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
  }

  &__cart {
    border-radius: clamp(10px, 2vw, 20px);
    background: #f8f8f8;
    padding: 20px;
    display: flex;
    flex-direction: column;
    gap: 30px;
    p {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 16px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--gray);
      margin: 0;
    }

    .btn {
      background: var(--blue);
      color: var(--accent);
      max-width: 230px;
      width: 100%;
      padding-block: 12px;
      @media (any-hover: hover) {
        &:hover {
          background: var(--accent);
          color: var(--white);
        }
      }
    }

    &:nth-child(1) {
      grid-column: span 2;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
    }

    &:last-child {
      grid-column: span 2;
      flex-direction: row;
    }
  }
}
